.delete-button {
  position: absolute;
  top: 10px;
  right: 8px;
  width: 18px;
  height: 18px;
  cursor: pointer;
  border-radius: 50%;
}
.delete-button:before,
.delete-button:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 18px;
  height: 1px;
  background-color: #333;
}
.delete-button:before:hover,
.delete-button:after:hover {
  background-color: #e62f57;
}
.delete-button:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.delete-button:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
