.category-list {
  position: relative;
  display: block;
  width: 120px;
  height: 39px;
  line-height: 3;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  -webkit-transition: 0.25s all ease;
  transition: 0.25s all ease;
}
.category-list:after {
  content: '\2193';
  position: absolute;
  color: #7e7e7e;
  top: -8px;
  right: 3px;
  padding: 0 10px;
  pointer-events: none;
  -webkit-transition: all 0.25s cubic-bezier(0.02, 0.01, 0.47, 1);
  transition: all 0.25s cubic-bezier(0.02, 0.01, 0.47, 1);
}
.category-list:hover .category-select {
  color: #333;
}
.category-list:hover:after {
  color: #333;
  top: -6px;
}
.category-select {
  position: absolute;
  background-color: #fff;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0 10px;
  color: #7e7e7e;
  font-size: 18px;
  cursor: pointer;
